@import url(https://stackpath.bootstrapcdn.com/bootstrap/4.3.1/css/bootstrap.min.css);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@600@400&display=swap);
.row{display:flex;flex-wrap:wrap;margin-right:0px;margin-left:0px}

header{background:#000;position:fixed;top:0;left:0;right:0;width:100%;z-index:99999;border-bottom:1px solid #2b2b2b}.logo{display:inline-block;color:#fff;font-size:13px;line-height:14px;font-family:'Open Sans', sans-serif}.logo .slogan{color:#ff681d;font-size:16px;font-weight:600}.headerList{list-style:none;padding:0;margin:0}.headerList li{display:inline-block;padding:6px 0 6px 6px}.headerList .search{opacity:0.6;margin-right:15px}.headerList .search img{width:15px;height:auto}.menu{margin-right:10px;display:inline-block;position:relative;top:-8px}.menu img{width:30px;height:auto}.menuList{position:fixed;left:0;top:45px;height:100%;width:0px;z-index:999;background:#ff681d;overflow-x:hidden}.menuList.active{width:170px}.menuList ul{list-style:none;padding:0;font-family:'Open Sans', sans-serif;margin:0}.menuList ul li{display:block}.menuList ul li:hover{background-color:#f55b0f}.menuList ul li a{font-family:'Open Sans', sans-serif;display:block;color:#fff !important;font-size:13px;line-height:13px;font-weight:500;padding:10px 15px;border-bottom:1px solid #f55b0f}

body{background-color:#000 !important}main{position:relative;padding-top:45px;background:#000;width:100%}.main-content-space{padding-top:10px}h1{font-family:'Open Sans', sans-serif;color:#fff;font-size:18px;font-weight:400}.list-1{list-style:none;padding:0;margin:0 -2px}.list-1 li{display:inline-block;width:25%;padding:0px 2px 4px 2px}@media screen and (max-width: 479px){.list-1 li{width:100%}}@media all and (max-width: 767px) and (min-width: 480px){.list-1 li{width:50%}}@media all and (max-width: 991px) and (min-width: 768px){.list-1 li{width:33.33%}}.box-1{font-family:'Open Sans', sans-serif;color:#fff;font-size:12px;line-height:16px;font-weight:400;background:rgba(143,149,230,0.2);padding:6px 8px 8px 8px;border-radius:2px}.box-1 .name{display:block}.box-1 .email{font-size:11px;display:block;opacity:0.8}.box-1 .c-btn-group{position:absolute;right:8px;bottom:9px}.box-1 .custom-btn{width:28px;height:28px;border:0;background-color:transparent;padding:0 0px 0 0;margin-left:8px}.box-1 .custom-btn img{width:28px;height:28px}footer{position:fixed;bottom:50px;text-align:center;left:0;right:0;width:100%;z-index:9999;color:#fff}.custom-btn{width:32px;height:32px;border:0;background-color:transparent;padding:0 0px 0 0;margin:0 6px}.custom-btn img{width:32px;height:32px}.zTiuq{width:100% !important;height:100% !important;position:relative;top:-2px}

